import React from 'react';
import { Col, Container, Row } from 'react-bootstrap';

const HeroArea = () => {
    return (
        <Container>
            <Row className="mt-5 ">
                <h3 className="text-center mb-2">About Us</h3>
                <Col sm={12} lg={5}>
                    <h1>We Provide you Best Drone in our Country</h1>
                    <p>Drones are more formally known as unmanned aerial vehicles (UAVs) or unmanned aircraft systems (UASes). Essentially, a drone is a flying robot that can be remotely controlled or fly autonomously through software-controlled flight plans in their embedded systems, working in conjunction with onboard sensors and GPS.</p>
                </Col>
                <Col className="p-4 " sm={12} lg={7} >
                    <img className="img-fluid" src="data:image/jpeg;base64," alt="" />
                </Col>
            </Row>
        </Container>
    );
};

export default HeroArea;